import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Untuk_akun } from 'src/app/sdk/hr/models/untuk-_akun';
import { Reimburse_item } from 'src/app/sdk/hr/models/reimburse-_item';
import { Reimburse } from 'src/app/sdk/hr/models/reimburse';
import { AkunReimburseService, AkunUntukAkunService, AkunReimburseItemService, DirektoriPegawaiMasterService, DirektoriVPegawaiPosisiService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core'
import { Pegawai_master, V_pegawai_posisi } from 'src/app/sdk/hr/models';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {
    constructor(
        private akunReimburseService: AkunReimburseService,
        private akunUntukAkunService: AkunUntukAkunService,
        private akunReimburseItemService: AkunReimburseItemService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
        private router: Router,
        private toolsLogFlowService: ToolsLogFlowService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    id_reimburse: string = ''
    dataReimburse: Reimburse = {}
    dataPegawai: Pegawai_master = {}
    dataUntuk: Untuk_akun = {}
    dataPegawaiPosisi: V_pegawai_posisi = {}
    dataReimburseItem: Reimburse_item[] = []

    ngOnInit() {
        this.id_reimburse = this.activatedRoute.snapshot.paramMap.get('id_reimburse')
        this.getReimburse()
        this.getReimburseItem()
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getReimburse()
                    this.getReimburseItem()
                    this.getLog()
                }
            }
        )


    }

    params: standarFlow = null;
    id_data_flow: string = ''

    getReimburse() {
        this.spinner.show()
        this.akunReimburseService.getAkunReimbursesId(this.id_reimburse).subscribe(
            data => {
                this.dataReimburse = data.data;
                this.getPegawai(data.data.id_pegawai);
                this.getUntuk(data.data.id_untuk);
                this.getPegawaiPosisi(data.data.id_pegawai);

                this.params = {
                    id: data.data.id_reimburse,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_reimburse"
                }
                this.standarApprovalService.param.next(this.params)

            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getUntuk(id_untuk: string) {
        this.akunUntukAkunService.getAkunUntukAkunsId(id_untuk).subscribe(
            data => {
                this.dataUntuk = data.data;
            }
        )
    }
    getPegawaiPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            data => {
                this.dataPegawaiPosisi = data.data;
            }
        )
    }


    getReimburseItem() {
        this.spinner.show()
        this.akunReimburseItemService.getAkunReimburseItems(JSON.stringify({ id_reimburse: this.id_reimburse })).subscribe(
            res => {
                this.dataReimburseItem = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_reimburse })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/ga/account/reimburse/list'])
    }

}
