import { Component, OnInit } from '@angular/core'
import {
    AkunReimburseService,
    DirektoriPegawaiMasterService,
    AkunReimburseItemService,
    AkunUntukAkunService,
} from 'src/app/sdk/hr/services'
import { NgxSpinnerService } from 'ngx-spinner'
import { NzNotificationService } from 'ng-zorro-antd'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Reimburse, Reimburse_item, Pegawai_master, Untuk_akun } from 'src/app/sdk/hr/models'
import { FormControl, Validators } from '@angular/forms'
import { SingleUploadService } from 'src/app/components/document/single-upload.service'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private akunReimburseService: AkunReimburseService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private akunReimburseItemService: AkunReimburseItemService,
        private akunUntukAkunService: AkunUntukAkunService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) {}

    toogleUpload = false

    dataReimburse: Reimburse = {}
    dataReimburseItem: Reimburse_item[] = []

    id_reimburse: string = ''

    listPegawai: Pegawai_master[] = []
    listUntuk: Untuk_akun[] = []

    ngOnInit() {
        this.id_reimburse = this.activatedRoute.snapshot.paramMap.get('id_reimburse')
        this.form()
        this.getData()
        this.getListPegawai()
        this.getListUntuk()


        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'akutansi_reimburse.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }

    togleShowUpload() {

        this.toogleUpload = true;
    }
    getListPegawai() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })
    }
    getListUntuk() {
        this.akunUntukAkunService.getAkunUntukAkuns().subscribe(data => {
            this.listUntuk = data.data
        })
    }

    //----------Anggota-----------------

    reimburse_item: any = ''
    tanggal_item: any = ''
    nilai: any = ''

    tambahItem() {
        if (this.reimburse_item == '') {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
            )
            return false
        }

        this.akunReimburseItemService
            .postAkunReimburseItems({
                reimburse_item: this.reimburse_item,
                id_reimburse: this.id_reimburse,
                tanggal_item: this.tanggal_item,
                nilai: this.nilai,
            })
            .subscribe(res => {
                this.getItem()
            })
    }

    listItem: any[] = []
    getItem() {
        let filter = {
            id_reimburse: this.id_reimburse,
        }
        this.akunReimburseItemService
            .getAkunReimburseItems(JSON.stringify(filter))
            .subscribe(res => {
                this.listItem = res.data
            })
    }
    deleteAnggota(id) {
        this.spinner.show()
        this.akunReimburseItemService.deleteAkunReimburseItemsId(id).subscribe(data => {
            this.getItem()
            this.spinner.hide()
        })
    }

    //----------Anggota-----------------

    getData() {
        this.spinner.show()
        this.akunReimburseService.getAkunReimbursesId(this.id_reimburse).subscribe(data => {
            this.dataForm.setValue(data.data)
            this.getItem()
            this.spinner.hide()

            this.singleUploadService.param.next({
                for_module: "akutansi_reimburse",
                for_table: "erp_akutansi.reimburse",
                for_module_id: data.data.id_reimburse
            })
        })
    }

    dataForm: FormGroupTypeSafe<Reimburse>
    form() {
        this.dataForm = this.fb.group<Reimburse>({
            id_pegawai: new FormControl(''),
            keterangan: new FormControl(''),
            total: new FormControl(''),
            tanggal_pengajuan: new FormControl(''),
            id_reimburse: new FormControl(''),
            id_untuk: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            validasi: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),
            status: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            id_data_flow: new FormControl(''),
            nomor: new FormControl(''),
            posisi: new FormControl(''),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()


        this.dataReimburse = this.dataForm.value
        this.akunReimburseService
            .putAkunReimbursesId({ id: this.id_reimburse, body: this.dataReimburse })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/ga/account/reimburse/list'])
    }
}
